.edit-page{
    box-sizing: border-box;
    position: relative;
    width: 100%;
    min-height: 100vh;

    background-color: rgba(0, 0, 0, 0.7);
    padding-top: 100px;
    padding-bottom: 600px;

    // backdrop-filter: blur(5px);

    >.all-conteiner{
        width: 900px;
        margin: 0 auto;
        border-radius: 15px;
        background-color: #0A0A0A;
        overflow: hidden;

        box-shadow: 0px 0px 20px 5px black;

        .edit-conteiner{
            padding: 15px 15px 0 15px ;
            box-sizing: border-box;
    
            width: 100%;
            margin: 0 auto;
            min-height: 300px;
            display: flex;
            justify-content: space-between;
    
            // border: 1px solid red;
    
            .book-image{
                box-sizing: border-box;
                position: relative;
                width: 291px;
                height: 450px;
                background-color: #2b2b2b;
    
                border-radius: 10px;
                // border: 1px solid red;
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
    
                .activeimage{
                    position: absolute;
                    top: 0;
                    left: 0;
    
                    box-sizing: border-box;
                    width: 100%;
                    height: 100%;
                    border-radius: 10px;
                    background-color: rgba(26, 26, 26, 0.70);
    
                    background-position: center;
                    background-repeat: no-repeat;
                    background-size: cover;
                    opacity: 1;
    
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    flex-direction: column;
    
                    >span{
                        font-family: "Inter";
                        color: rgb(252, 99, 99);
                        font-size: 34px;
                        &:nth-child(2){
                            font-size: 26px; 
                        }
                    }
                }
    
                input{
                    cursor: pointer;
                    position: absolute;
                    display: block;
                    top: 0;
                    left: 0;
    
                    border-radius: 10px;
                    opacity: 0;
    
                    width: 100%;
                    height: 100%;
    
                }
    
                &.haveimage{
                    background-image: url('./../../assets/imageback.png');
                    .activeimage{
                        opacity: 0.6;
                        &:hover{
                            opacity: 0.4;
                        }
                    }
                }
            }
            .right-collomn{
                width: 559px;
                input.text{
                    box-sizing: border-box;
                    display: flex;
                    width: 100%;
                    height: 40px;
    
                    background-color: #1A1A1A;
                    margin-bottom: 10px;
                    outline: none;
                    border: none;
                    border-radius: 10px;
                    padding-left: 15px;
    
                    font-family: 'Inter';
                    font-style: normal;
                    font-weight: 400;
                    color: white;
    
                    &.serias-name{
                        font-size: 26px;
                    }
                    &.authtor-name{
                        font-size: 22px;
                    }
                }
    
                .textarea{
                    position: relative;
                    box-sizing: border-box;
    
                    flex-grow: 1;
                    width: 100% !important;
                    border-radius: 10px;
                    height: 350px !important;
                    background-color: #1A1A1A;
                    padding: 10px 15px 40px 15px;
    
                    textarea{
                    width: 100% !important;
                    height: 100% !important;
                    background-color: #1A1A1A;
                    outline: none;
                    border: none;
                    overflow-y: scroll;
                    margin: 0;
    
                    font-family: 'Inter';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 20px;
                    color: white;
    
                    &::-webkit-scrollbar {
                        width: 0px;               /* ширина scrollbar */
                    }
                    scrollbar-width: thin;          /* "auto" или "thin"  */
                    scrollbar-color: rgba(0, 0, 255, 0) rgba(255, 166, 0, 0);
                    }
    
                    >span{
                        user-select: none;
                        position: absolute;
                        right: 15px;
                        bottom: 10px;
    
                        float: right;
                        font-family: 'Inter';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 18px;
                        color: rgb(175, 175, 175);
                    }
                }
            }
        }

        // border: 1px solid red;

        .buttons-group{
            box-sizing: border-box;
            padding: 15px;

            width: 100%;
            height: 50px;
            // border: 1px solid red;
            margin-bottom: 15px;

            display: flex;
            justify-content: space-between;

            div{
                cursor: pointer;
                user-select: none;
                border-radius: 10px;
                flex-grow: 1;
                height: 50px;
                text-align: center;

                background-color: #191919;
                line-height: 50px;
                font-family: 'Inter';
                font-style: normal;
                font-weight: 400;
                font-size: 30px;
                margin-right: 15px;

                transition: 0.2s;
                &:hover{
                    background-color: #2b2b2b;
                }
                &:nth-child(1){
                    color: #77ff62;
                }
                &:nth-child(2){
                    color: #fadf44;
                }
                &:nth-child(3){
                    color: #ff3d3d;
                    margin: 0;
                }
            }
        }
        .books-group{
            box-sizing: border-box;
            padding: 15px;
            width: 100%;
            min-height: 40px;
            border-radius: 10px;
            background-color: #0A0A0A;
            // border: 1px solid red;

            .box-editor-fragments{
                width: 100%;
                min-height: 40px;
            }
        }
    }

    &.ongrag{
        .activeimage{
            opacity: 0.5;
        }
    }
    &:not(.ongrag){
        .activeimage{
            // transition: 0.01s 0.01s;
        }
    }
    .error-message{
        user-select: none;
        position: fixed;

        top: calc(50% - 100px);
        left: calc(50% - 250px);

        width: 500px;
        height: 200px;
        border-radius: 10px;
        background-color: #0A0A0A;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        color: rgb(255, 83, 83);
        font-family: 'Inter';
        font-style: normal;
        font-weight: 400;

        line-height: 38px;

        span:nth-child(1){
            font-size: 30px;
        }
        span:nth-child(2){
            color: white;
            font-size: 24px;
        }
        span:nth-child(3){
            line-height: 18px;
            font-size: 20px;
            opacity: 0.7;
        }
    }
    .audiobook-link{
        position: absolute;
        top: 30px;
        left: 30px;
        color: white;

        font-family: 'Inter';
        font-style: normal;
        font-weight: 500;
        font-size: 30px;
        line-height: 40px;
        cursor: pointer;
    }
}